import template from './sw-settings-cumulated-weight.html.twig';

const { Context, Component, Mixin } = Shopware;
const { Criteria } = Shopware.Data;

export default {
    template,


    inject: ['repositoryFactory','filterFactory'],

    mixins: [
        Mixin.getByName('listing')
    ],

    data() {
        return {
            data: [],
            sortBy: 'label',
            sortDirection: 'DESC',
            isLoading: false,
            filterCriteria: [],
            defaultFilters: [
            ],
            storeKey: 'grid.filter.cumulated.weight',
            activeFilterNumber: 0,
        };
    },

    metaInfo() {
        return {
            title: this.$createTitle(),
        };
    },

    watch: {
        defaultCriteria: {
            handler() {
                this.getList();
            },
            deep: true,
        },

    },

    computed: {
        orderLineItemRepository() {
            return this.repositoryFactory.create('order_line_item');
        },

        cumulatedWeightColumns() {
            return this.getCumulatedWeightColumns();
        },

        defaultCriteria() {
            const criteria = new Criteria(this.page, this.limit);

            criteria.setTerm(this.term);

            this.sortBy.split(',').forEach(sortBy => {
                criteria.addSorting(Criteria.sort(sortBy, this.sortDirection));
            });

            criteria.addFilter(Criteria.equals('type', 'product'));

            this.filterCriteria.forEach(filter => {
                criteria.addFilter(filter);
            });

            criteria
                .addAssociation('product')

            return criteria;
        },

        listFilters() {
            return [];
        },
    },

    created() {
        this.createdComponent();
    },

    methods: {
        createdComponent() {
            this.getList();
        },

        async getList() {
            this.isLoading = true;

            const criteria = await Shopware.Service('filterService')
                .mergeWithStoredFilters(this.storeKey, this.defaultCriteria);

            this.activeFilterNumber = criteria.filters.length;

            this.orderLineItemRepository.search(criteria, Context.api).then((result) => {
                this.total = result.total;
                this.data = this.cumulate(result);
                this.isLoading = false;

                return Promise.resolve();
            }).catch(() => {
                this.isLoading = false;
            });
        },

        cumulate(result){
            let tmp = [];

            result.forEach((item) => {

                let addedIdx = tmp.findIndex((e) => e.number === item.payload.productNumber);

                if(addedIdx === -1){
                    addedIdx = tmp.push({
                        label: item.label,
                        number: item.payload.productNumber,
                        quantity: 0,
                        weight: 0,
                        options: {}
                    }) -1;
                }

                tmp[addedIdx]['quantity'] += item.quantity;
                tmp[addedIdx]['options'] = item.payload.options;

                if(item.product.weight !== null){
                    tmp[addedIdx]['weight'] += item.quantity * item.product.weight;
                }
            });
            return tmp;
        },

        updateCriteria(criteria) {
            this.page = 1;
            this.filterCriteria = criteria;
        },

        getCumulatedWeightColumns(){
            return[
                {
                    property: 'label',
                    label: 'Produkt',
                },
                {
                    property: 'number',
                    label: 'Produktnummer',
                },
                {
                    property: 'quantity',
                    label: 'Anzahl',
                },
                {
                    property: 'weight',
                    label: 'Gewicht',
                },
            ];
        },

    }
};
